header {
  box-sizing: border-box;
  padding: 0 35px 0 30px;
  font-family: "Rubik", sans-serif;
  letter-spacing: 1px;
  font-size: 20px;
  font-weight: normal;
  background-color: #282828;
  justify-content: space-between;
}

.header-logo {
  width: 120px;
  height: 120px;
}

header ul {
  margin: 0;
  padding: 0;
  list-style-type: none;
}

header ul li {
  list-style-type: none;
  display: inline-block;
  color: white;
}

nav > ul > a {
  display: inline-block;
  height: 125px;
  font-size: 15px;
  font-weight: 400;
  line-height: 125px;
  margin-left: 40px;
  position: relative;
  text-decoration: none;
}

header a {
  text-decoration: none;
  color: #454545;
}

nav > ul > li {
  display: inline-block;
  height: 125px;

  font-size: 15px;
  font-weight: 400;
  line-height: 125px;
  margin-left: 40px;
  position: relative;
  text-decoration: none;
}

nav > ul > li:last-child {
  margin-left: 60px;
}

.nav-others:hover .dropDown-others {
  visibility: visible;
  height: 100px;
}
.nav-quiz:hover .dropDown-quiz {
  visibility: visible;
  height: 300px;
}

nav li:hover {
  color: red;
}

nav a:hover {
  color: red;
}

.nav-game:hover .dropDown-games {
  visibility: visible;
  height: 105px;
}

.dropDown {
  box-shadow: 0 1px 7px -2px rgba(0, 0, 0, 0.3);
  transition: all 0.3s ease 0s;
  position: absolute;
  visibility: hidden;
  background-color: #282828;
  top: 123px;
  z-index: 15;
  height: 0;
  overflow: hidden;
}

.dropDown-others,
.dropDown-games {
  width: 130px;
  line-height: 50px;
}

.dropDown-quiz {
  width: 150px;
  line-height: 50px;
}

.dropDown a {
  display: block;
}

.dropDown li,
.dropDown i {
  margin-left: 10px;
}

.dropDown a {
  color: white;
}
